nav{
    width: 100%;
    margin: 0;
    background: #0f0f0f;
    position: fixed;
    top: -0rem;
}

.nav-container{
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fcfcfc;
}

.name{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-inline: 1rem;
    font-size: 0.9rem;
}

.hamburger-menu{
    width: 2rem;
    height: 2rem;
    background: #fcfcfc;
    border-radius: 0.7em;
}

.hamburger-menu img{
    height: 100%;
    width: 100%;
}

.drop-down-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #fcfcfc;
    position: absolute;
    right: 3%;
    top: 4.2em;
    width: 12rem;
    border-radius: .5em;
    padding: 0.5em;
}

.drop-down-container::before{
    content: "";
    background: #fcfcfc;
    width: 1em;
    height: 1em;
    transform: rotate(45deg);
    position: relative;
    right: -5rem;
    top: -0.9rem;
}

.nav-links-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 90%;
}

.nav-links-container a{
    color: #fcfcfc;
    text-decoration: none;
    padding: 0.4em 0.6em;
    background: #0f0f0f;
    width: 90%;
    margin-top: 0.5em;
    font-size: 1rem;
    border-radius: 0.5em;
}

.nav-button{
    margin-block: 1em;
}

.nav-button button{
    padding: 0.8em 5em;
    font-size: 0.8rem;
    border-radius: 0.5em;
    border: none;
    outline: none;
    background: #0f0f0f;
    color: #fcfcfc;
}

.desktop-nav{
    display: none;
}

@media(width > 1250px){
    .name{
        padding-inline: 3rem;
        font-size: 1.2rem;
    } 

    .drop-down-container{
        display: none;
    }

    .hamburger-menu{
        display: none;
    }

    .desktop-nav{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 50%;
        padding-inline-end: 2rem;
    }

    .desktop-links a{
        color: #fcfcfc;
        text-decoration: none;
        margin-inline: 1em;
        padding: 0.6em 1em;
        font-size: 1.2rem;
        border-radius: 1em;
    }

    .desktop-links a:hover{
        background: #fcfcfc;
        color: #0f0f0f;
    }

    .desktop-nav-button button{
        padding: 0.5em 2em;
        background: #0f0f0f;
        color: #fcfcfc;
        border: 1px solid #fcfcfc;
        border-radius: 1em;
        font-size: 1rem;
    }
}